/* Стили для старых страниц */
@import "variables.scss";
html {height: 100%;}
body {
    height: 100%;
    min-height: 100%;
}
footer div, .bottom div, #headerWrap div, #menuPanel div {
    font-family: $roboto;
}
#pageWrap {
    width: 100%;
    min-width: 320px;
    position: relative;
    overflow: hidden;
    background: $white;
    @include tablet {
        min-height: 100%;
        margin: 0 0 -500px 0;
    }
    @include desktop {
        margin: 0 0 -565px 0;
    }
}
#headerWrap {
    padding: 15px 0;
    @include gradient(-60deg, $bluedark, $bluelight);
    color: $white;
    @include tablet {
        padding: 25px 0;
    }
    @include desktop {
        padding: 50px 0;
    }
}
#page {
    position: relative;
    @include tablet {
        min-height: 1000px;
        padding: 0 0 500px 0;
    }
    @include desktop {
        padding: 0 0 565px 0;
    }
}
#footerWrap {
    position: relative;
    @include gradient(-55deg, $blue, $blueblack);
    color: $white;
    @include tablet {
        height: 500px;
        margin: -500px 0 0 0;
    }
    @include desktop {
        height: 565px;
        margin: -565px 0 0 0;
    }
}
.openMenu {
    height: 100%;
    overflow: hidden;
}
.widthFix {
    max-width: 1660px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    @include desktop {
        padding: 0 40px;
    }
}
.icon {
    display: block;
    @include iconSize(60px);
    fill: $white;
}
@import "header.scss";
@import "footer.scss";
header {
    .logo {
        padding: 0;
        float: none;
        height: auto;
        a {
            padding: 0;
        }
    }
    .navBlock {
        a {
            color: $white;
        }
        ul {
            margin: 0;
        }
        .companyMenu {
            height: 25px;
            a {
                font: 500 15px/16px $fira;
                @include desktop {
                    font: 500 17px/24px $fira;
                }
                @include desktopHD {
                    font: 500 20px/26px $fira;
                }
                @include fullSize {
                    font: 500 22px/26px $fira;
                }
            }
            @include desktop {
                height: 30px;
            }
        }
        .productMenu {
            height: 25px;
            a {
                font: 500 12px/15px $fira;
                @include desktop {
                    font: 500 14px/20px $fira;
                }
                @include desktopHD {
                    font: 500 16px/24px $fira;
                }
                @include fullSize {
                    font: 500 18px/24px $fira;
                }
            }
            @include desktop {
                height: 30px;
            }
        }
    }
}
#menuPanel {
    box-sizing: border-box; -moz-box-sizing: border-box; -webkit-box-sizing: border-box;
    a {
        font: inherit;
    }
    .topMenu {
        ul {
            list-style: none;
            li {
                a {
                    font: 500 20px/1.2 $fira;
                    color: $white;
                }
            }
            &.subMenu {
                a {
                    font: 400 18px/1.4 $fira;
                }
            }
        }
    }
    .searchInp {
        input {
            box-sizing: border-box; -moz-box-sizing: border-box; -webkit-box-sizing: border-box;
        }
    }
    @include desktop {
        .topMenu {
            ul {
                li {
                    a {
                        font: 500 24px/1.2 $fira;
                    }
                }
                &.subMenu {
                    a {
                        font: 500 20px/1.2 $fira;
                    }
                }
            }
        }
    }
    @include desktopHD {
        .topMenu {
            ul {
                li {
                    a {
                        font: 500 26px/1.2 $fira;
                    }
                }
                &.subMenu {
                    a {
                        font: 500 22px/1.2 $fira;
                    }
                }
            }
        }
    }
}
footer {
    font: normal 15px/1.5 $roboto;
    @include tablet {
        height: 340px;
    }
    @include desktop {
        height: 329px;
    }
    .logo {
        float: none;
        padding: 0;
    }
    .contactInfo {
        .phone {
            a {
                font: inherit;
                color: $white;
            }
        }
    }
    .footMenu {
        a {
            color: $white;
        }
    }
}
.bottom {
    .pimax {
        color: $white;
        font: normal 14px/1.4 $roboto;
        @include laptop {
            font: normal 15px/1.4 $roboto;
        }
    }
}