header {
    @include flex(center,space-between,'');
    position: relative;
    .logo {
        a {
            display: block;
            width: 150px;
            height: 60px;
            background: url(../img/logo-white.png) center center no-repeat;
            //background: url(../img/logo-blue.png) center center no-repeat;
            background-size: contain;
        }
    }
    .navBlock {
        display: none;
    }
    .icons {
        > div {
            margin: 15px 0 0 0;
            &:first-child {
                margin: 0;
            }
        }
        .icon {
            @include iconSize(30px);
            cursor: pointer;
            transition: $transition;
            &:hover {
                opacity: .7;
            }
        }
    }
    @include tablet {
        .logo {
            min-width: 180px;
            max-width: 180px;
            a {
                width: 180px;
                height: 75px;
            }
        }
    }
    @include laptop {
        .navBlock {
            display: block;
            flex-grow: 1;
            text-transform: uppercase;
            text-shadow: 0 0 15px rgba(0,0,0,.4);
            white-space: nowrap;
            margin: 0 55px 0 0;
            a {
                text-decoration: none;
                border-bottom: 1px solid $white;
                &.mark {
                    border-bottom: none;
                    @include gradient(0deg,$blue,$bluelight);
                    border-radius: 3px;
                    padding: 4px 8px;
                }
                &:hover {
                    border-bottom: 1px solid transparent;
                    &.mark {
                        border-bottom: none;
                        @include gradient(180deg,$blue,$bluelight);
                    }
                }
            }
            ul {
                @include flex(center,flex-end,'',wrap);
            }
            .companyMenu {
                font: 500 15px/16px $fira;
                height: 40px;
                padding: 15px 0 0 0;
                overflow-y: hidden;
                margin: 0 0 5px 0;
                li {
                    margin: 0 10px 15px 14px;
                }
            }
            .productMenu {
                font: 500 12px/15px $fira;
                height: 40px;
                padding: 15px 0 0 0;
                overflow: hidden;
                margin: 0 0 5px 0;
                li {
                    margin: 0 10px 15px 9px;
                }
            }
        }
        .icons {
            position: absolute;
            right: 20px;
            top: 0;
            > div {
                margin: 13px 0;
                &:first-child {
                    margin: 13px 0;
                }
            }
        }
    }
    @include desktop {
        .logo {
            min-width: 200px;
            max-width: 200px;
            a {
                width: 200px;
                height: 80px;
            }
        }
        .navBlock {
            margin: 0 90px 0 0;
            .companyMenu {
                font: 500 17px/24px $fira;
                height: 45px;
                li {
                    margin: 0 20px 15px 20px;
                }
                a {
                    border-bottom: 2px solid rgba(255,255,255,.8);

                    &:hover {
                        border-bottom: none;
                    }
                }
            }
            .productMenu {
                font: 500 14px/20px $fira;
                height: 45px;
                li {
                    margin: 0 20px 15px 17px;
                }
            }
        }
        .icons {
            @include flex('','',row-reverse,'');
            right: 40px;
            top: 12px;
            > div {
                display: inline-block;
                vertical-align: middle;
                margin: 0 0 0 15px;
                &:first-child {
                    margin: 0 0 0 20px;
                }
            }
        }
    }
    @include desktopHD {
        .logo {
            min-width: 280px;
            max-width: 280px;
            a {
                width: 280px;
                height: 115px;
            }
        }
        .navBlock {
            .companyMenu {
                font: 500 20px/26px $fira;
                margin: 0 0 10px 0;
                li {
                    margin: 0 20px 15px 18px;
                }
            }
            .productMenu {
                font: 500 16px/24px $fira;
            }
        }
        .icons {
            top: 17px;
            > div {
                margin: 0 0 0 20px;
            }
        }
    }
    @include fullSize {
        .logo {
            min-width: 330px;
            max-width: 330px;
            a {
                width: 330px;
                height: 130px;
            }
        }
        .icons {
            top: 25px;
        }
        .navBlock {
            .companyMenu {
                font: 500 22px/26px $fira;
                li {
                    margin: 0 20px 15px 23px;
                }
            }
            .productMenu {
                font: 500 18px/24px $fira;
            }
        }
    }
}
.menuIco {
    width: 30px;
    height: 28px;
    position: relative;
    z-index: 12;
    cursor: pointer;
    transition: $transition;
    span {
        display: block;
        width: 100%;
        height: 5px;
        position: absolute;
        left: 0;
        top: 11px;
        background: $white;
        transition: $transition;
        &:before, &:after {
            content: '';
            display: block;
            width: 100%;
            height: 5px;
            position: absolute;
            left: 0;
            background: $white;
            transition: $transition;
        }
        &:before {
            top: -11px;
        }
        &:after {
            top: 11px;
        }
    }
    &.open {
        span {
            background: transparent;
            &:before {
                top: 0;
                transform: rotate(45deg);
            }
            &:after {
                top: 0;
                transform: rotate(-45deg);
            }
        }
    }
}
#menuPanel {
    width: 100%;
    height: 100%;
    padding: 50px 0 15px 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 11;
    background: $bluemiddle;
    color: $white;
    overflow-x: hidden;
    overflow-y: auto;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;
    opacity: 0;
    visibility: hidden;
    transition: all .5s ease;
    &::-webkit-scrollbar {
        width: 0;
    }
    .widthFix {
        min-height: 100%;
    }
    .topMenu {
        margin: 0 0 30px 0;
        ul {
            font: 500 20px/1.2 $fira;
            margin: 0 0 20px 0;
            li {
                margin: 0 0 20px 0;
                a {
                    text-decoration: none;
                    text-transform: uppercase;
                    position: relative;
                    border-bottom: 1px solid $white;
                    &:hover {
                        border-bottom: 1px solid transparent;
                    }
                }
            }
            &.subMenu {
                font: 400 18px/1.4 $fira;
                margin: 0 0 0 15px;
                li {
                    margin: 20px 0 0 0;
                }
            }
        }
    }
    .socBlock {
        .socIco {
            margin: 0 20px 0 0;
        }
    }
    .topSearch {
        margin: 0 0 30px 0;
    }
    .searchInp {
        position: relative;
        input {
            position: relative;
            display: block;
            width: 100%;
            border: 2px solid $white;
            background: none;
            font: 400 20px/38px $fira;
            padding: 0 15px 0 40px;
            color: $white;
        }
        ::-webkit-input-placeholder {color: $white;}
        ::-moz-placeholder {color: $white;}
        :-moz-placeholder {color: $white;}
        :-ms-input-placeholder {color: $white;}
        .icon {
            position: absolute;
            left: 13px;
            top: 50%;
            margin: -10px 0 0 0;
            @include iconSize(20px);
        }
    }
    &.open {
        opacity: 1;
        visibility: visible;
    }
    @include tablet {
        padding: 25px 0 0 0;
        .topSearch {
            margin: 0 80px 30px 0;
        }
        .topMenu {
            @include flex('', space-between,row,wrap);
            margin: 0 50px 0 0;
            ul {
                margin: 0 30px 0 0;
            }
        }
        .socBlock {
            width: 60px;
            position: absolute;
            bottom: 20px;
            right: 20px;
            .socIco {
                margin: 0 0 20px 0;
            }
        }
    }
    @include laptop {
        //height: auto;
        padding: 30px 0;
        //position: absolute;
        overflow-y: hidden;
        .topMenu {
            @include flex('','','',nowrap);
            ul {
                &.subMenu {
                    margin: 0 0 0 25px;
                    li {
                        margin: 25px 0 0 0;
                    }
                }
            }
        }
        .socBlock {
            width: 60px;
            position: absolute;
            bottom: 0;
            right: 20px;
            .socIco {
                margin: 0 0 20px 0;
            }
        }
        .searchInp {
            margin: 0 0 40px 0;
            input {
                font: 400 24px/50px $fira;
                padding: 0 20px 0 60px;
            }
            .icon {
                left: 15px;
                margin: -15px 0 0 0;
                @include iconSize(30px);
            }
        }
    }
    @include desktop {
        padding: 50px 0 30px 0;
        .topMenu {
            margin: 0 100px 0 0;
            ul {
                font: 500 24px/1.2 $fira;
                &.subMenu {
                    font: 500 20px/1.2 $fira;
                    margin: 0 0 0 35px;
                }
            }
        }
        .socBlock {
            right: 40px;
        }
        .topSearch {
            margin: 0 100px 60px 0;
        }
    }
    @include desktopHD {
        padding: 55px 0 30px 0;
        .topMenu {
            margin: 0 160px 0 75px;
            ul {
                font: 500 26px/1.2 $fira;
                &.subMenu {
                    font: 500 22px/1.2 $fira;
                }
            }
        }
        .topSearch {
            margin: 0 190px 60px 0;
        }
        .searchInp {
            input {
                font: 400 30px/64px $fira;
            }
            .icon {
                left: 15px;
                margin: -17px 0 0 0;
                @include iconSize(35px);
            }
        }
    }
}