footer {
    min-width: 320px;
    max-width: 1660px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    .logo {
        display: block;
        width: 165px;
        height: 70px;
        margin: 0 auto 20px auto;
        background: url(../img/logo-white.png) center center no-repeat;
        background-size: contain;
    }
    .contactInfo {
        margin: 0 0 30px 0;
        > div {
            margin: 0 0 15px 0;
        }
        .phone {
            font: 500 13px/1.4 $roboto;
            margin: 0 0 10px 0;
            a {
                text-decoration: none;
            }
        }
    }
    .socBlock {
        margin: 0 0 10px 0;
        .socIco {
            margin: 0 5px;
        }
        .icon {
            @include iconSize(40px);
        }
        .title {
            display: none;
        }
    }
    .footBan {
        display: none;
    }
    .footMenu {
        text-transform: uppercase;
        .productMenu {
            display: none;
        }
        ul {
            margin: 0 0 30px 0;
            li {
                margin: 0 0 15px 0;
            }
        }
        a {
            font: 500 13px/1.4 $fira;
            text-decoration: none;
            border-bottom: 2px solid transparent;
            transition: $transition;
            &.mark {
                padding: 5px 10px;
                border-radius: 3px;
                margin: -5px 0 0 -12px;
                border: 2px solid $white;
                &:hover {
                    background: $white;
                    color: $bluedark;
                    border: 2px solid $white;
                    border-bottom: none;
                }
            }
            &:hover, &.current {
                border-bottom: 2px solid $white;
            }
        }
    }
    @include tablet {
        height: 420px;
        text-align: left;
        padding: 60px 20px 20px 20px;
        @include flex('',space-between,row,wrap);
        .logo {
            width: 190px;
            height: 80px;
            margin: 0 0 25px 0;
        }
        .contactInfo {
            min-width: 275px;
            max-width: 35%;
            > div {
                margin: 0 0 10px 12px;
            }
            .phone {
                margin: 0 0 10px 12px;
                font: 500 17px/1.4 $roboto;
            }
        }
        .rightBlock {
            width: 100%;
            @include flex(flex-start,space-between);
            padding: 0 17px;
        }
        .socBlock {
            .title {
                display: block;
            }
            .socIco {
                margin: 0 15px 0 0;
            }
        }
        .footBan {
            display: block;
            width: 175px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .footMenu {
            @include flex('',space-around,row);
            flex-grow: 1;
            .productMenu {
                display: block;
            }
            ul {
                margin: 0 0 0 30px;
                li {
                    margin: 0 0 20px 0;
                }
            }
        }
    }
    @include laptop {
        padding: 60px 60px 20px 60px;
        .contactInfo {
            min-width: 400px;
            max-width: 400px;
        }
        .footMenu {
            @include flex('',space-between,'');
            max-width: 50%;
            ul {
                margin: 0 0 0 60px;
            }
        }
    }
    @include desktop {
        height: 459px;
        padding: 110px 40px 20px 40px;
        @include flex('','','',nowrap);
        .logo {
            width: 245px;
            height: 100px;
            margin: 0 0 45px 0;
        }
        .contactInfo {
            margin: 0;
            > div {
                margin: 0 0 15px 17px;
            }
            .phone {
                margin: 0 0 15px 17px;
            }
        }
        .rightBlock {
            width: auto;
            max-width: 300px;
            padding: 0;
            display: block;
        }
        .socBlock {
            margin: 0 0 40px 0;
            white-space: nowrap;
            .socIco {
                margin: 0 15px 0 0;
            }
        }
        .footBan {
            width: auto;
        }
        .footMenu {
            margin: 0 30px;
            font: 500 15px/1.4 $fira;
            flex-grow: 1;
            @include flex('',space-around,'');
            max-width: none;
            ul {
                margin: 0 30px;
            }
        }
    }
    @include desktopHD {
        .footMenu {
            a {
                font: 500 18px/1.4 $fira;
            }
        }
    }
}
.footLine {
    border-top: 1px solid $bluewhite;
}
.bottom {
    text-align: center;
    padding: 20px;
    font: normal 14px/1.4 $roboto;
    .copy {
        margin: 0 0 20px 0;
    }
    .pimax {
        text-decoration: none;
        white-space: nowrap;
        opacity: .75;
        transition: $transition;
        img {
            vertical-align: text-bottom;
        }
        &:hover {
            opacity: 1;
        }
    }
    @include  tablet {
        text-align: left;
        @include flex(center,space-between,row);
        height: 79px;
        .copy {
            margin: 0 30px 0 0;
        }
    }
    @include laptop {
        padding: 20px 77px;
        font: normal 15px/1.4 $roboto;
    }
    @include desktop {
        height: 105px;
        padding: 0 40px;
        .copy {
            margin: 0 0 0 30px;
        }
        .pimax {
            margin: 0 55px 0 0;
        }
    }
}
.socBlock {
    font-size: 0;
    .title {
        text-transform: uppercase;
        font: 500 15px/1.4 $fira;
        margin: 0 0 15px 0;
    }
    .socIco {
        display: inline-block;
        vertical-align: middle;
        transition: $transition;
        &:hover {
            opacity: .7;
        }
    }
    .icon {
        @include iconSize(45px);
    }
    @include desktop {
        .title {
            font: 500 18px/1.4 $fira;
            margin: 0 0 20px 0;
        }
        .icon {
            @include iconSize(50px);
        }
    }
}